import React from 'react';
import clsx from 'clsx';
import { makeStyles } from '@material-ui/core/styles';
import Drawer from '@material-ui/core/Drawer';
import List from '@material-ui/core/List';
import Divider from '@material-ui/core/Divider';
import IconButton from '@material-ui/core/IconButton';
import ChevronLeftIcon from '@material-ui/icons/ChevronLeft';
import AccountBoxOutlinedIcon from '@material-ui/icons/AccountBoxOutlined';
import ListItemIcon from '@material-ui/core/ListItemIcon'
import ListItem from '@material-ui/core/ListItem'
import { ListItemText } from '@material-ui/core';
import ImportContactsOutlinedIcon from '@material-ui/icons/ImportContactsOutlined';
import HouseOutlinedIcon from '@material-ui/icons/HouseOutlined';
import { Link } from 'react-router-dom';

const drawerWidth = 240;

const useStyles = makeStyles((theme) => ({
  toolbarIcon: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'flex-end',
    padding: '0 8px',
    ...theme.mixins.toolbar,
  },
  drawerPaper: {
    position: 'relative',
    whiteSpace: 'nowrap',
    width: drawerWidth,
    transition: theme.transitions.create('width', {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
    backgroundColor: '#37474f'
  },
  drawerPaperClose: {
    overflowX: 'hidden',
    transition: theme.transitions.create('width', {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
    width: theme.spacing(7),
    [theme.breakpoints.up('sm')]: {
      width: theme.spacing(9),
    },
  },
  drawerStyle: {
    backgroundColor: '#37474f'
  }
}));

export default function Sivupalkki({ open, handleDrawerClose }) {
  const classes = useStyles();

  return (
    <div className={classes.drawerStyle}>
      <Drawer
        variant="permanent"
        classes={{
          paper: clsx(classes.drawerPaper, !open && classes.drawerPaperClose),
        }}
        open={open}
        
      >
        <div className={classes.toolbarIcon}>
          <IconButton onClick={handleDrawerClose}>
            <ChevronLeftIcon style={{color:'white'}} />
          </IconButton>
        </div>
        <Divider />
        <List>
          <ListItem button component={Link} to="/" >
            <ListItemIcon>
                <HouseOutlinedIcon style={{color:'white'}} />
            </ListItemIcon>
            <ListItemText primary="Resumé" style={{color:'white'}}/>
          </ListItem>
          <ListItem button component={Link} to="/Projects">
            <ListItemIcon>
                <ImportContactsOutlinedIcon style={{color:'white'}}/>
            </ListItemIcon>
            <ListItemText primary="Projects" style={{color:'white'}}/>
          </ListItem>
          <ListItem button component={Link} to="/About">
            <ListItemIcon>
                <AccountBoxOutlinedIcon style={{color:'white'}}/>
            </ListItemIcon>
            <ListItemText primary="About me" style={{color:'white'}}/>
          </ListItem>
        </List>
        <Divider />
      </Drawer>
      </div>
  );
}