import React, { useState, useRef } from 'react';
import { makeStyles, useTheme } from '@material-ui/core/styles';
// import Card from '@material-ui/core/Card';
// import CardContent from '@material-ui/core/CardContent';
// import CardMedia from '@material-ui/core/CardMedia';
// import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import Paper from '@material-ui/core/Paper';
import TextField from '@material-ui/core/TextField';
// import { MuiPickersUtilsProvider, DateTimePicker } from '@material-ui/pickers';
// import DateFnsUtils from '@date-io/date-fns';
// import { fi } from 'date-fns/locale';
import Grid from '@material-ui/core/Grid';
import Button from '@material-ui/core/Button';
import { useHistory } from 'react-router-dom'
import { v4 as uuid } from 'uuid';

const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
    padding: 20
  },
//   details: {
//     display: 'flex',
//     flexDirection: 'column',
//   },
//   content: {
//     flex: '1 0 auto',
//   },
//   cover: {
//     width: 151,
//   },
}));

export default function Koulutus({ studies, setStudies }) {
  const classes = useStyles();
  const history = useHistory();
  const lomake = useRef({});

  const kasitteleLomake = (e) => {
        e.preventDefault();

        let newStudy = {
            id: uuid(),
            school: lomake.current.school,
            startTime: lomake.current.startTime,
            endTime: lomake.current.endTime,
            field: lomake.current.field
        }

        setStudies([...studies, newStudy])

        history.push("/")
    }

    const syoteKasittelija = (e) => {
        lomake.current[e.target.name] = e.target.value;
    }

    return (
        <form onSubmit={kasitteleLomake}>
            <Paper>
                <div className={classes.root}>         
                        <Grid container spacing={2}>
                            <Grid item xs={12}>
                                <Typography paragraph variant="h6">
                                    Lisää uusi tutkinto
                                </Typography>
                            </Grid>
                            <Grid item xs={6}>
                                <TextField
                                    label="Koulu"
                                    variant="outlined"
                                    fullWidth
                                    size="small"
                                    name="school"
                                    onChange={syoteKasittelija}
                                />
                            </Grid>
                            <Grid item xs={6}>
                                <TextField
                                    label="Tutkinto"
                                    variant="outlined"
                                    fullWidth
                                    size="small"
                                    name="field"
                                    onChange={syoteKasittelija}
                                />
                            </Grid>
                            <Grid item xs={6}>
                                <TextField
                                    label="Aloitusaika"
                                    variant="outlined"
                                    fullWidth
                                    size="small"
                                    name="startTime"
                                    onChange={syoteKasittelija}
                                />
                            </Grid>
                            <Grid item xs={6}>
                                <TextField
                                    label="Päättymisaika"
                                    variant="outlined"
                                    fullWidth
                                    size="small"
                                    name="endTime"
                                    onChange={syoteKasittelija}
                                />
                            </Grid>
                            <Grid item xs={1}>
                                <Button
                                    variant="contained"
                                    color="primary"
                                    type="submit"
                                    onClick={kasitteleLomake}
                                >
                                    Lisää
                                </Button>
                            </Grid>
                        </Grid>
                </div>
            </Paper>
        </form>
  );
}