import React, { useState, useRef } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import Paper from '@material-ui/core/Paper';
import TextField from '@material-ui/core/TextField';
import { MuiPickersUtilsProvider, DatePicker } from '@material-ui/pickers';
import DateFnsUtils from '@date-io/date-fns';
import { fi } from 'date-fns/locale';
import Grid from '@material-ui/core/Grid';
import Button from '@material-ui/core/Button';
import { useHistory } from 'react-router-dom';
import { v4 as uuid } from 'uuid';

const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
    padding: 20
  },
}));

export default function Koulutus({ jobs, setJobs }) {
  const classes = useStyles();
  const [alku, setAlku] = useState(new Date())
  const [loppu, setLoppu] = useState(new Date())
  const history = useHistory();
  const lomake = useRef({});

  const lomakeKasittelija = (e) => {
    e.preventDefault();

    const newJob = {
        id: uuid(),
        employer: lomake.current.employer,
        jobTitle: lomake.current.jobTitle,
        startDate: alku,
        endDate: loppu,
        description: lomake.current.description
    }

    setJobs([...jobs, newJob])
    history.push("/")
  }

  const syoteKasittelija = (e) => {
    lomake.current[e.target.name] = e.target.value;
    }

  return (
    <Paper>
        <div className={classes.root}>   
            <MuiPickersUtilsProvider utils={DateFnsUtils} locale={fi}>
                <Grid container spacing={2}>
                    <Grid item xs={12}>
                        <Typography paragraph variant="h6">
                            Lisää uusi työ
                        </Typography>
                    </Grid>
                    <Grid item xs={6}>
                        <DatePicker
                            label="Alkamisaika"
                            inputVariant="outlined"
                            fullWidth
                            size="small"
                            cancelLabel="Peruuta"
                            ampm={false}
                            value={alku}
                            onChange={setAlku}
                            format="dd.MM.y"
                            name="alku"
                        />
                    </Grid>
                    <Grid item xs={6}>
                        <DatePicker
                            label="Päättymisaika"
                            inputVariant="outlined"
                            fullWidth
                            size="small"
                            cancelLabel="Peruuta"
                            ampm={false}
                            value={loppu}
                            onChange={setLoppu}
                            format="dd.MM.y"
                            name="loppu"
                        />
                    </Grid>
                    <Grid item xs={6}>
                        <TextField
                            label="Työnantaja"
                            variant="outlined"
                            fullWidth
                            size="small"
                            name="employer"
                            onChange={syoteKasittelija}
                        />
                    </Grid>
                    <Grid item xs={6}>
                        <TextField
                            label="Titteli"
                            variant="outlined"
                            fullWidth
                            size="small"
                            name="jobTitle"
                            onChange={syoteKasittelija}
                        />
                    </Grid>
                    <Grid item xs={12}>
                        <TextField
                            label="Kuvaus"
                            variant="outlined"
                            fullWidth
                            size="small"
                            name="description"
                            onChange={syoteKasittelija}
                        />
                    </Grid>
                    <Grid item xs={4}>
                        <Button
                            variant="contained"
                            color="primary"
                            type="submit"
                            onClick={lomakeKasittelija}
                        >
                            Lisää
                        </Button>
                    </Grid>
                </Grid>
            </MuiPickersUtilsProvider>
        </div>
    </Paper>
  );
}