import React, { useState, useRef } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import Paper from '@material-ui/core/Paper';
import TextField from '@material-ui/core/TextField';
import Grid from '@material-ui/core/Grid';
import Button from '@material-ui/core/Button';
import Slider from '@material-ui/core/Slider';
import { useHistory } from 'react-router-dom'
import { v4 as uuid } from 'uuid';

const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
    padding: 20
  },
}));

export default function Koulutus({ techs, setTechs }) {
  const classes = useStyles();
  const history = useHistory();
  const lomake = useRef({});
  const [taitotaso, setTaitotaso] = useState()

  const kasitteleLomake = (e) => {
    e.preventDefault();
    
    let newTech = {
        id: uuid(),
        title: lomake.current.title,
        value: parseInt(taitotaso)
    }

    setTechs([...techs, newTech])
  
    history.push("/")
  }

  const syoteKasittelija = (e) => {
      lomake.current[e.target.name] = e.target.value;
  }
console.log(taitotaso)
  return (
    <form onSubmit={kasitteleLomake}>
      <Paper className={classes.root}>
          <Grid container spacing={2}>
            <Grid item xs={12}>
              <Typography paragraph variant="h6">
                Lisää uusi teknologia
              </Typography>
            </Grid>
            <Grid item xs={12}>
              <TextField
                label="Teknologia"
                variant="outlined"
                fullWidth
                size="small"
                name="title"
                onChange={syoteKasittelija}
              />
            </Grid>
            <Grid item xs={12}>
              <Typography variant="body2">Osaamistaso (%)</Typography>
              <Slider
                defaultValue={50}
                name="value"
                aria-labelledby="discrete-slider-small-steps"
                step={5}
                marks
                min={0}
                max={100}
                valueLabelDisplay="auto"
                onChange={(e, value) => setTaitotaso(value)}
              />
            </Grid>
            <Grid item xs={4}>
              <Button
                variant="contained"
                color="primary"
                type="submit"
              >
                Lisää
              </Button>
            </Grid>
          </Grid>
      </Paper>
    </form>
  );
}