import React, { useRef } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import Paper from '@material-ui/core/Paper';
import TextField from '@material-ui/core/TextField';
import Grid from '@material-ui/core/Grid';
import Button from '@material-ui/core/Button';
import { useHistory } from 'react-router-dom'
import { v4 as uuid } from 'uuid';

const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
    padding: 20
  },
}));

export default function Taito({ skills, setSkills }) {
  const classes = useStyles();
  const history = useHistory();
  const lomake = useRef({});

  const kasitteleLomake = (e) => {
      e.preventDefault();

      let newSkill = {
          id: uuid(),
          label: lomake.current.label,
      }

      setSkills([...skills, newSkill])

      history.push("/")
  }

  const syoteKasittelija = (e) => {
      lomake.current[e.target.name] = e.target.value;
  }

  return (
    <form onSubmit={kasitteleLomake}>
      <Paper className={classes.root}>
        <Grid container spacing={1}>
          <Grid item xs={12}>
            <Typography paragraph variant="h6">
              Lisää uusi taito
            </Typography>
          </Grid>
          <Grid item xs={9}>
            <TextField
              label="Taito"
              variant="outlined"
              fullWidth
              size="small"
              name="label"
              onChange={syoteKasittelija}
            />
          </Grid>
          <Grid item xs={3}>
            <Button
              variant="contained"
              color="primary"
              type="submit"
            >
              Lisää
            </Button>
          </Grid>
        </Grid>
      </Paper>
    </form>
  );
}