import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import CssBaseline from '@material-ui/core/CssBaseline';
import Etusivu from './Etusivu';
import Ylapalkki from './Ylapalkki';
import Sivupalkki from './Sivupalkki';
import { BrowserRouter as Router, Route } from 'react-router-dom';
import Projektit from './Projektit';
import Yhteystiedot from './Yhteystiedot';
import Kirjautuminen from './Login';

const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
  },
  content: {
    flexGrow: 1,
    height: '100vh',
    overflow: 'auto',
  },
}));

export default function Navigointi() {
  const classes = useStyles();
  const [open, setOpen] = React.useState(true);
  const [loggedIn, setLoggedIn] = React.useState(false);

  const handleDrawerOpen = () => {
    setOpen(true);
  };

  const handleDrawerClose = () => {
    setOpen(false);
  };

  const handleLogout = () => {
    setLoggedIn(false);
  }

  return (
    <div className={classes.root}>
      <Router>
        <CssBaseline />
        <Ylapalkki open={open} handleDrawerOpen={handleDrawerOpen} loggedIn={loggedIn} setLoggedIn={setLoggedIn}/>
        <Sivupalkki open={open} handleDrawerClose={handleDrawerClose}  />
        <main className={classes.content}>
          <Route exact path="/"><Etusivu loggedIn={loggedIn} /></Route>
          <Route exact path="/Projects"><Projektit loggedIn={loggedIn} /></Route>
          <Route exact path="/About" component={Yhteystiedot} />
          <Route exact path="/Login"><Kirjautuminen loggedIn={loggedIn} setLoggedIn={setLoggedIn} /></Route>
        </main>
      </Router>
    </div>
  );
}