import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import DeleteForeverIcon from '@material-ui/icons/DeleteForever';

const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
    marginBottom: 10
  },
  content: {
    flex: '1 0 auto',
  },
}));

export default function Koulutus({ studies, setStudies, loggedIn }) {
  const classes = useStyles();

  const handleDelete = (id) => {
    const filtered = studies.filter((study) => study.id !== id)
    setStudies(filtered)
  }

  return (
    <>
      {studies.map((study) => {
        return (
          <Card className={classes.root} key={study.id}>
              <CardContent className={classes.content}>
                <Typography component="h6" variant="h6">
                  {study.school}
                </Typography>
                <Typography variant="subtitle2" color="textSecondary">
                  {study.field}, {study.startTime} - {study.endTime}
                </Typography>
                {loggedIn ? (
                <IconButton onClick={() => handleDelete(study.id)}>
                  <DeleteForeverIcon/>
                </IconButton>
                ): (<></>)}
              </CardContent>
          </Card>
        )
      })}
    </>
  );
}