import React, { useRef, useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import Paper from '@material-ui/core/Paper';
import TextField from '@material-ui/core/TextField';
import Grid from '@material-ui/core/Grid';
import Button from '@material-ui/core/Button';
import { useHistory } from 'react-router-dom';
import Container from '@material-ui/core/Container';
import { Link } from 'react-router-dom';

const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
    marginTop: 80,
    padding: 20
  },
  details: {
    display: 'flex',
    flexDirection: 'column',
  },
  content: {
    flex: '1 0 auto',
  },
  cover: {
    width: 151,
  },
}));

export default function Kirjautuminen({ loggedIn, setLoggedIn }) {
  const classes = useStyles();
  const history = useHistory();
  const lomake = useRef({});
  const [virheilmoitus, setVirheilmoitus] = useState({});

  const handleLogin = (e) => {
    e.preventDefault();

    let virheet = {}

    if(!lomake.current.username || !lomake.current.password) {
      virheet = {...virheet, username: "Täytä kaikki kentät!"}
    }

    if(lomake.current.username !== "Teppo" || lomake.current.password !== "Testaaja") {
      virheet = {...virheet, username: "Tarkista tiedot!"}
    }

    if(Object.entries(virheet).length > 0) {
      setVirheilmoitus({...virheet})
    } else {
      setLoggedIn(true);

      history.push("/")
    }
  }

  const handleLogout = () => {
    setLoggedIn(false);

    history.push("/");
  }

  const syoteKasittelija = (e) => {
      lomake.current[e.target.name] = e.target.value;
  }

  return (
    loggedIn ? ( 
      <Container maxWidth="sm">
          <Paper className={classes.root}>
            <Grid container spacing={1}>
            <Grid item xs={12}>
                <Typography paragraph variant="h6">
                  Haluatko varmasti kirjautua ulos?
                </Typography>
            </Grid>
            <Grid item xs={3}>
                <Button
                  variant="contained"
                  color="primary"
                  onClick={() => handleLogout()}
                  fullWidth
                >
                  Ok
                </Button>
            </Grid>
            <Grid item xs={3}>
                <Button
                  variant="contained"
                  color="primary"
                  component={Link}
                  to="/"
                  fullWidth
                >
                  Peruuta
                </Button>
            </Grid>
          </Grid>
        </Paper>
      </Container>
    ) : (
    <form onSubmit={handleLogin}>
        <Container maxWidth="sm">
          <Paper className={classes.root}>
            <Grid container spacing={1}>
            <Grid item xs={12}>
              <Typography paragraph variant="h6">
                Kirjaudu sisään
              </Typography>
            </Grid>
            <Grid item xs={4}>
                <TextField
                  label="Käyttäjätunnus"
                  variant="outlined"
                  fullWidth
                  size="small"
                  name="username"
                  onChange={syoteKasittelija}
                  error={Boolean(virheilmoitus.username)}
                  helperText={virheilmoitus.username}
                />
            </Grid>
            <Grid item xs={4}>
                <TextField
                  label="Salasana"
                  variant="outlined"
                  fullWidth
                  size="small"
                  name="password"
                  type="password"
                  onChange={syoteKasittelija}
                  error={Boolean(virheilmoitus.password)}
                  helperText={virheilmoitus.password}
                />
            </Grid>
            <Grid item xs={3}>
                <Button
                variant="contained"
                color="primary"
                type="submit"
                >
                Lisää
                </Button>
            </Grid>
            </Grid>
        </Paper>
      </Container>
    </form>
    )
  )
}