import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import DeleteForeverIcon from '@material-ui/icons/DeleteForever';

const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
    marginBottom: 10
  },
  content: {
    flex: '1 0 auto',
  },
}));

export default function Koulutus({ courses, setCourses, loggedIn }) {
  const classes = useStyles();

  const handleDelete = (id) => {
    const filtered = courses.filter((course) => course.id !== id)
    setCourses(filtered)
  }

  return (
    <>
      {courses.map((course) => {
        return (
          <Card className={classes.root} key={course.id}>
              <CardContent className={classes.content}>
                  <Typography component="h6" variant="h6">
                    {course.course}, {course.school}
                  </Typography>
                  <Typography variant="subtitle2" color="textSecondary">
                    {course.startTime} - {course.endTime}
                  </Typography>
                  {loggedIn ? (
                  <IconButton onClick={() => handleDelete(course.id)}>
                    <DeleteForeverIcon/>
                  </IconButton>
                  ): (<></>)}
              </CardContent>
          </Card>
        )
      })}
    </>
  );
}