import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import Paper from '@material-ui/core/Paper';
// import Projektikortit from './Projektikortti';
import Kuva from '../img/kuva.jpg';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import Divider from '@material-ui/core/Divider';
import DeveloperBoardIcon from '@material-ui/icons/DeveloperBoard';
import FacebookIcon from '@material-ui/icons/Facebook';
import InstagramIcon from '@material-ui/icons/Instagram';
import GitHubIcon from '@material-ui/icons/GitHub';
import EditIcon from '@material-ui/icons/Edit';
import StorageIcon from '@material-ui/icons/Storage';
import SecurityIcon from '@material-ui/icons/Security';
import IconButton from '@material-ui/core/IconButton';
import LinkedInIcon from '@material-ui/icons/LinkedIn';

const useStyles = makeStyles((theme) => ({
  appBarSpacer: theme.mixins.toolbar,
  root: {
    height: '100vh',
  },
  content: {
    flexGrow: 1,
    overflow: 'auto',
  },
  container: {
    paddingTop: theme.spacing(4),
    paddingBottom: theme.spacing(4),
    margin:80
  },
  image: {
    backgroundImage: `url(${Kuva})`,
    backgroundRepeat: 'no-repeat',
    backgroundColor:
      theme.palette.type === 'light' ? theme.palette.grey[50] : theme.palette.grey[900],
    backgroundSize: 'cover',
    backgroundPosition: 'center',
  },
  paper: {
    margin: theme.spacing(8, 4),
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'left',
    color: 'white'
  },
}));

export default function Projektit() {
  const classes = useStyles();

  return (
    <div className={classes.appBarSpacer}>
      <div className={classes.content}>
        <Container maxWidth="lg" className={classes.container}>
          <Grid container component="main" className={classes.root}>
            <Grid item xs={false} sm={4} md={6} className={classes.image} />
            <Grid item xs={12} sm={8} md={6} component={Paper} elevation={6} square style={{ backgroundColor:'#37474f' }}>
              <div className={classes.paper}>
                <Grid item xs={12}>
                  <Typography variant="h4">Tietoa minusta</Typography>
                </Grid>
                <Grid item xs={12}>
                  <Typography variant="subtitle2" paragraph style={{color:'lightgreen'}}>35 v / full stack web developer</Typography>
                </Grid>
                <Grid item xs={12}>
                  <Typography variant="body2" paragraph style={{color:'white'}}>
                    Laatua, käyttäjäystävällisyyttä, tyylikkyyttä ja tuloksellisuutta arvostava, intohimoinen full stack sovelluskehittäjä. Taustalla on useapia projekteja niin 
                    harrastuspohjalta kuin oikeille asiakkaille oikeiseen käyttöön tulleista sovelluksistakin. Pidän itseni kehittämisestä ja uuden oppimisesta. Tällä hetkellä olen
                    työssä, mutta olen aina valmis ottamaan uusia sivuprojekteja. Myös pelinkehityksen eri osa-alueilta.
                  </Typography>
                </Grid>
                <Divider style={{ backgroundColor: 'gray', margin:10}} />
                <Grid item xs={12}>
                  <Typography paragraph variant="h6">Palveluni</Typography>
                </Grid>
                <Grid container direction="row" justify="center" alignItems="center" spacing={8}>
                  <Grid item xs={6}>
                    <DeveloperBoardIcon />
                    <Typography paragraph variant="body1" style={{color:'lightgreen'}}>Sovelluskehitys</Typography>
                    <Typography variant="body2">Erilaisten sovellusten kehittäminen tarpeidesi ja toiveidesi mukaisesti.</Typography>
                  </Grid>
                  <Grid item xs={6}>
                    <EditIcon />
                    <Typography paragraph variant="body1" style={{color:'lightgreen'}}>Graafinen suunnittelu</Typography>
                    <Typography variant="body2">Erilaiset graafiset suunnitelmat ja toteutukset, kuvankäsittely, pikseli- ja vektorigrafiikka sekä 3D-mallinnus.</Typography>
                  </Grid>
                  <Grid item xs={6}>
                    <StorageIcon />
                    <Typography paragraph variant="body1" style={{color:'lightgreen'}}>Tietokanta</Typography>
                    <Typography paragraph variant="body2">Tietokantojen asennukset, konfiguroinnit ja yhteyksien luominen sovelluksesi ja tietokannan välille</Typography>
                  </Grid>
                  <Grid item xs={6}>
                    <SecurityIcon />
                    <Typography paragraph variant="body1" style={{color:'lightgreen'}}>Tietoturva</Typography>
                    <Typography paragraph variant="body2">Tietoturvan arvioinnit ja strategian laatiminen</Typography>
                  </Grid>
                </Grid>
                <Divider style={{ backgroundColor: 'gray', margin:10}} />
                <Grid container direction="row" justify="center" alignItems="center" >
                  <Grid item xs={1}>
                    <IconButton href="https://www.facebook.com/mirka.poikelin" style={{color:'white'}}>
                      <FacebookIcon />
                    </IconButton>
                  </Grid>
                  <Grid item xs={1}>
                    <IconButton style={{color:'white'}} href="https://www.linkedin.com/in/mirka-poikelin-23198a190/">
                      <LinkedInIcon />
                    </IconButton>
                  </Grid>
                  <Grid item xs={1}>
                    <IconButton style={{color:'white'}} href="https://www.instagram.com/sha3dyn/">
                      <InstagramIcon />
                    </IconButton>
                  </Grid>
                  <Grid item xs={1}>
                    <IconButton style={{color:'white'}} href="https://github.com/Sha3dyn">
                      <GitHubIcon />
                    </IconButton>
                  </Grid>
                </Grid>
              </div>
            </Grid>
          </Grid>
        </Container>
      </div>
    </div>
  );
}