import React, { useRef } from 'react';
import { makeStyles} from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import Paper from '@material-ui/core/Paper';
import TextField from '@material-ui/core/TextField';
import Grid from '@material-ui/core/Grid';
import Button from '@material-ui/core/Button';
import { useHistory } from 'react-router-dom'
import { v4 as uuid } from 'uuid';

const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
    padding: 20
  },
}));

export default function Koulutus({ courses, setCourses}) {
    const classes = useStyles();
    const history = useHistory();
    const lomake = useRef({});

    const kasitteleLomake = (e) => {
        e.preventDefault();

        let newCourse = {
            id: uuid(),
            school: lomake.current.school,
            course: lomake.current.course,
            startTime: lomake.current.startTime,
            endTime: lomake.current.endTime,
        }

        setCourses([...courses, newCourse])

        history.push("/")
    }

    const syoteKasittelija = (e) => {
        lomake.current[e.target.name] = e.target.value;
    }

    return (
      <form onSubmit={kasitteleLomake}>
        <Paper>
            <div className={classes.root}>
                <Grid container spacing={2}>
                    <Grid item xs={12}>
                        <Typography paragraph variant="h6">
                            Lisää uusi kurssi
                        </Typography>
                    </Grid>
                    <Grid item xs={6}>
                        <TextField
                            label="Kurssi"
                            variant="outlined"
                            fullWidth
                            size="small"
                            name="course"
                            onChange={syoteKasittelija}
                        />
                    </Grid>
                    <Grid item xs={6}>
                        <TextField
                            label="Koulu"
                            variant="outlined"
                            fullWidth
                            size="small"
                            name="school"
                            onChange={syoteKasittelija}
                        />
                    </Grid>
                    <Grid item xs={6}>
                        <TextField
                            label="Aloitusaika"
                            variant="outlined"
                            fullWidth
                            size="small"
                            name="startTime"
                            onChange={syoteKasittelija}
                        />
                    </Grid>
                    <Grid item xs={6}>
                        <TextField
                            label="Päättymisaika"
                            variant="outlined"
                            fullWidth
                            size="small"
                            name="endTime"
                            onChange={syoteKasittelija}
                        />
                    </Grid>
                    <Grid item xs={1}>
                        <Button
                            variant="contained"
                            color="primary"
                            type="submit"
                        >
                            Lisää
                        </Button>
                    </Grid>
                </Grid>
            </div>
        </Paper>
    </form>
    );
}