import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import Tyohistoria from './Tyohistoria';
import Koulutus from './Koulutus';
import Kurssit from './Kurssit';
import Teknologiat from './Teknologiat';
import Osaaminen from './Osaaminen';
import Otsikko from './Otsikko';
import LisaaTyo from './lisaaTyo';
import LisaaKoulu from './lisaaKoulutus';
import LisaaKurssi from './lisaaKurssi';
import LisaaTeknologia from './lisaaTeknologia';
import LisaaTaito from './lisaaTaito';
import { v4 as uuid } from 'uuid';

const useStyles = makeStyles((theme) => ({
  appBarSpacer: theme.mixins.toolbar,
  container: {
    paddingTop: theme.spacing(4),
    paddingBottom: theme.spacing(4),
  },
}));

export default function Etusivu({ loggedIn }) {
  const classes = useStyles();
  const [jobs, setJobs] = React.useState([
    {
      id: uuid(),
      employer: 'Riversoft Oy',
      startDate: new Date(2020, 9, 28),
      endDate: new Date(),
      jobTitle: 'Full stack developer',
      description: 'Full stack developer'
    }
  ])

  const [studies, setStudies] = React.useState([
    {
      id: uuid(),
      school: 'Kaakkois-Suomen ammattikorkeakoulu',
      startTime: 'syksy 2020',
      endTime: 'jatkuu',
      field: 'Tradenomi, tietojenkäsittely'      
    }
  ])

  const [courses, setCourses] = React.useState([
    {
      id: uuid(),
      course: 'Elements of AI',
      school: 'Helsingin yliopisto',
      startTime: 'syksy 2020',
      endTime: 'jatkuu',
    }
  ])

  const [techs, setTechs] = React.useState([
    {
      id: uuid(),
      title: 'C#',
      value: 75
    }
  ])

  const [skills, setSkills] = React.useState([
    {
      id: uuid(),
      label: 'Adobe Photoshop'
    }
  ])

  return (
    <>
        <div className={classes.appBarSpacer} />
        <Container maxWidth="lg" className={classes.container}>
          <Grid
            container
            direction="row"
            justify="flex-start"
            alignItems="flex-start"
            spacing={3}
          >
            <Grid item xs={8}>
              <Grid
                container
                spacing={2}
              >
                <Grid  item xs={12}>
                <Otsikko title="Työhistoria" />
                </Grid>
                <Grid item xs={12}>
                  <Paper style={{ backgroundColor: 'lightgreen' }}>
                    <Tyohistoria jobs={jobs} setJobs={setJobs} loggedIn={loggedIn} />
                  </Paper>
                </Grid> 
                {loggedIn ? (         
                <Grid item xs={12}>
                  <LisaaTyo jobs={jobs} setJobs={setJobs} />
                </Grid> ) : (<></>)}
                <Grid  item xs={12}>
                  <Otsikko title="Koulutus" />
                </Grid>
                <Grid item xs={12}>
                    <Koulutus studies={studies} setStudies={setStudies} loggedIn={loggedIn} />
                </Grid>
                {loggedIn ? (
                <Grid item xs={12}>
                  <LisaaKoulu studies={studies} setStudies={setStudies} />
                </Grid> ) : (<></>)}
                <Grid  item xs={12}>
                  <Otsikko title="Kurssit" />
                </Grid>
                <Grid item xs={12}>
                    <Kurssit courses={courses} setCourses={setCourses} loggedIn={loggedIn} />
                </Grid>
                {loggedIn ? (
                <Grid item xs={12}>
                  <LisaaKurssi courses={courses} setCourses={setCourses} />
                </Grid> ) : (<></>)}
              </Grid>
            </Grid>
            <Grid item xs={4}>
              <Grid
                container
                spacing={2}
              >
                <Grid item xs={12}>
                  <Teknologiat techs={techs} setTechs={setTechs} loggedIn={loggedIn} />
                </Grid>
                {loggedIn ? (
                <Grid item xs={12}>
                  <LisaaTeknologia techs={techs} setTechs={setTechs} />
                </Grid> ) : (<></>)}
                <Grid item xs={12}>
                  <Osaaminen skills={skills} setSkills={setSkills} loggedIn={loggedIn} />
                </Grid>
                {loggedIn ? (
                <Grid item xs={12}>
                  <LisaaTaito skills={skills} setSkills={setSkills} />
                </Grid>  ) : (<></>)}
              </Grid>
            </Grid>
          </Grid>
        </Container>
    </>
  );
}