import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Accordion from '@material-ui/core/Accordion';
import AccordionDetails from '@material-ui/core/AccordionDetails';
import AccordionSummary from '@material-ui/core/AccordionSummary';
import Typography from '@material-ui/core/Typography';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import IconButton from '@material-ui/core/IconButton';
import DeleteForeverIcon from '@material-ui/icons/DeleteForever';
import Grid from '@material-ui/core/Grid';
import { format } from 'date-fns';

const useStyles = makeStyles((theme) => ({
  root: {
    width: '100%',
  },
  heading: {
    fontSize: theme.typography.pxToRem(15),
    flexBasis: '33.33%',
    flexShrink: 0,
  },
  secondaryHeading: {
    fontSize: theme.typography.pxToRem(15),
    color: theme.palette.text.secondary,
  },
}));

export default function Tyohistoria({ jobs, setJobs, loggedIn }) {
  const classes = useStyles();
  const [expanded, setExpanded] = React.useState(false);

  const handleChange = (panel) => (event, isExpanded) => {
    setExpanded(isExpanded ? panel : false);
  };

  const handleDelete = (id) => {
    const filtered = jobs.filter((job) => job.id !== id)
    setJobs(filtered)
  }

  return (
    <div container className={classes.root}>
      {jobs.map((job) => {
        return (
          <Accordion expanded={expanded === job.id} onChange={handleChange(job.id)} key={job.id}>
            <AccordionSummary
              expandIcon={<ExpandMoreIcon />}
              aria-controls="panel1bh-content"
              id="panel1bh-header"
            >
              <Typography className={classes.heading}>{job.jobTitle}, {job.employer}</Typography>
              <Typography className={classes.secondaryHeading}>{format(job.startDate, 'dd.MM.y')} - {format(job.endDate, 'dd.MM.y')}</Typography>
            </AccordionSummary>
            <AccordionDetails>
              <Grid container spacing={4}>
                <Grid item xs={12}>
                  <Typography>
                    {job.description}
                  </Typography>
                  </Grid>
                  {loggedIn ? (
                  <Grid item xs={12}>
                  <IconButton onClick={() => handleDelete(job.id)}>
                    <DeleteForeverIcon/>
                  </IconButton>
                </Grid>
                ): (<></>)}
              </Grid>
            </AccordionDetails>
          </Accordion>
        )
      })}
    </div>
  );
}