import React, { useRef } from 'react';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
// import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
// import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import { makeStyles} from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
// import Paper from '@material-ui/core/Paper';
// import Container from '@material-ui/core/Container';
import Typography from '@material-ui/core/Typography';
import { useHistory } from 'react-router-dom'
import { v4 as uuid } from 'uuid';


const useStyles = makeStyles((theme) => ({
    root: {
      display: 'flex',
      padding: 20
    },
}));

export default function LisaaProjekti({ projects, setProjects, open, setOpen }) {
//    const [open, setOpen] = React.useState(false);
    const classes = useStyles();
    const history = useHistory();
    const lomake = useRef({});

    const kasitteleLomake = (e) => {
        e.preventDefault();

        let newProject = {
            id: uuid(),
            title: lomake.current.title,
            description: lomake.current.description,
            image: lomake.current.image,
            projectUrl: lomake.current.projectUrl,
            publishUrl: lomake.current.publishUrl
        }

        setProjects([...projects, newProject])

        history.push("/projects")
    }

    const syoteKasittelija = (e) => {
        lomake.current[e.target.name] = e.target.value;
    }

  const handleClose = () => {
    setOpen(false);
  };

  return (
    <div>
      <Dialog open={open} onClose={handleClose} aria-labelledby="form-dialog-title">
        <DialogTitle id="form-dialog-title">Lisää uusi projekti</DialogTitle>
        <DialogContent>
            <form onSubmit={kasitteleLomake}>
                        <div className={classes.root}>
                            <Grid container spacing={2}>
                                <Grid item xs={12}>
                                    <TextField
                                        label="Otsikko"
                                        variant="outlined"
                                        fullWidth
                                        size="small"
                                        name="title"
                                        onChange={syoteKasittelija}
                                    />
                                </Grid>
                                <Grid item xs={12}>
                                    <TextField
                                        label="Kuvaus"
                                        variant="outlined"
                                        fullWidth
                                        size="small"
                                        name="description"
                                        onChange={syoteKasittelija}
                                    />
                                </Grid>
                                <Grid item xs={12}>
                                    <TextField
                                        label="Kuvan polku"
                                        variant="outlined"
                                        fullWidth
                                        size="small"
                                        name="image"
                                        onChange={syoteKasittelija}
                                    />
                                </Grid>
                                <Grid item xs={12}>
                                    <TextField
                                        label="Projektin polku"
                                        variant="outlined"
                                        fullWidth
                                        size="small"
                                        name="projectUrl"
                                        onChange={syoteKasittelija}
                                    />
                                </Grid>
                                <Grid item xs={12}>
                                    <TextField
                                        label="Sovelluksen polku"
                                        variant="outlined"
                                        fullWidth
                                        size="small"
                                        name="publishUrl"
                                        onChange={syoteKasittelija}
                                    />
                                </Grid>
                                <Grid item xs={12} />
                                <Grid item xs={6}>
                                    <Button
                                        variant="contained"
                                        color="primary"
                                        type="submit"
                                        fullWidth
                                    >
                                        Lisää
                                    </Button>
                                </Grid>
                                <Grid item xs={6}>
                                    <Button
                                        variant="contained"
                                        color="primary"
                                        fullWidth
                                    >
                                        Peruuta
                                    </Button>
                                </Grid>
                            </Grid>
                        </div>
        </form>
        </DialogContent>
      </Dialog>
    </div>
  );
}