import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import Button from '@material-ui/core/Button';
import Projektikortit from './Projektikortti';
import Kuva from '../img/projekti.png';
import { v4 as uuid } from 'uuid';
import LisaaProjekti from './lisaaProjekti';

const useStyles = makeStyles((theme) => ({
  appBarSpacer: theme.mixins.toolbar,
  content: {
    flexGrow: 1,
    height: '100vh',
    overflow: 'auto',
  },
  container: {
    paddingTop: theme.spacing(4),
    paddingBottom: theme.spacing(4),
    margin:80
  },
}));

export default function Projektit({ loggedIn }) {
  const classes = useStyles();
  const [open, setOpen] = React.useState(false);
  const [projects, setProjects] = React.useState([
    {
      id: uuid(),
      title: 'Verkostoitumisjärjestelmä',
      description: 'Helsingin yliopiston Web-palvelinohjelmointi 2020 -kurssin lopputyö, jossa piti tehdä LinkedIniä vastaava verkostoitumispalvelu. Tehtävässä tuli tehdä sekä Backend että Frontend käyttäen Java-ohjelmointikieltä.',
      image: `${Kuva}`,
      projectUrl: 'https://github.com/Sha3dyn/Wepa-projekti',
      publishUrl: 'https://stark-crag-75293.herokuapp.com/login'
    }
  ])

  const handleClickOpen = () => {
    setOpen(true);
  };

  return (
    <div className={classes.appBarSpacer}>
      <div className={classes.content}>
        <Container maxWidth="lg" className={classes.container}>
        {loggedIn ? (
          <div style={{marginBottom: 10}}>
            <Button variant="contained" color="primary" onClick={() => handleClickOpen()}>Lisää</Button>
          </div>) : (<></>)}
          <Projektikortit projects={projects} setProjects={setProjects} loggedIn={loggedIn} />
        </Container>
      </div>
      <LisaaProjekti projects={projects} setProjects={setProjects} open={open} setOpen={setOpen} />
    </div>
  );
}